// 引入定位
import { addressdata } from '../../common/list.js';
// 引入SDK核心类
var QQMapWX = require('../../common/qqmap-wx-jssdk.js');
var qqmapsdk = new QQMapWX({
	key: 'M5IBZ-FPCHS-NM6OI-6CY27-IN2J7-H7FJG'
});
export default {
	name: 'citying',
	data() {
		return {
			citynone: true,
			inputValue:'',
			address: '',
			citydata: [], //搜索的城市
			keywoeds: '',
			pageroute: '', //从哪个页面进来的路由
			city: [
				{
					name: '湖南工程学院'
				},
				{
					name: '湖南工程学院-北门'
				},
				{
					name: '滨江学生公寓'
				},
				{
					name: '湖南工程学院分院-体育馆'
				},
				{
					name: '湖南工程学院(南校区)'
				},
				{
					name: '湖南工程学院北苑红楼'
				},
			],
		};
	},
	methods:{
		//搜索触发
		searchCity(e){
			console.log(e)
			this.citynone = false
		},
		//点击取消，关闭搜索
		canCal(){
			this.citynone = true
			this.inputValue = ''
			this.citydata = ''
		},
		//点击定位到城市名称
		clickCity(){
			// console.log(this.address)
			let cityion = this.address
			this.backRoutes(cityion)
		},
		//取到热门景点
		hotCity(city){
			this.backRoutes(city)
		},
		//搜索城市
		seekCity(city){
			console.log(city)
			this.backRoutes(city)
		},
		
		//跳转到攻略页面
		backRoutes(cityion){
			//vuex传值
			if(this.pageroute == 'pages/travels/travels'){
				//传给发表页面
				console.log(cityion)
				this.$store.commit('travemuta',cityion)
			}else{
				//传给tab攻略页面
				console.log(cityion)
				this.$store.commit('citymuta',cityion)
			}
			
			uni.navigateBack({
				delta:1
			})
		},
		//实时搜索城市
		searchInput(e){
			qqmapsdk.getSuggestion({
				keyword:e.detail.value ,
				policy:1,
				// filter:'category=大学',
				// region:"湘潭市",
				success:(res)=>{
					let city = res.data
					this.citydata = city.map((item)=>{
						return item.title
					})
				},
				fail:(err)=>{
					console.log(err)
				}
			})
		},
		//定位当前城市
		addRess(){
			addressdata()
			.then((res)=>{
				console.log(res)
				this.address = res.result.address_reference.landmark_l2.title
			})
			.catch((err)=>{
				console.log("用户拒绝定位")
				this.address = '新校区'
			})
		}
	},
	mounted() {
		this.addRess()
	},
	//路由判断
	onLoad() {
		let pages = getCurrentPages()
		let prevpage = pages[pages.length - 2];
		console.log(prevpage.route)
		this.pageroute = prevpage.route
	}
};
